
#home-fabricante .wrapper-contenido {
  background-color: #eeeeee;
}

#home-fabricante .links-home {
  color: var(--azul-abi);
  font-size: 1.25rem;
  font-weight: 400;
  gap: 1rem;
  padding: 1rem 0;
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
}

#home-fabricante .links-home span {
  cursor: pointer;
  padding: 0.625rem;
}

#home-fabricante .izquierdo, #home-fabricante .derecho {
  text-align: center;
  width: 50%;
}

#home-fabricante .izquierdo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#home-fabricante .titulo, #home-fabricante .subtitulo, #home-fabricante .txt-subtitulo {
  color: var(--azul-abi);
  text-align: center;
}

#home-fabricante .titulo {
  font-size: 2rem;
  font-weight: 500;
}

#home-fabricante .subtitulo {
  font-size: 2rem;
  font-weight: 400;
  padding-bottom: 1rem;
}

#home-fabricante .txt-subtitulo {
  font-size: 1.25rem;
  font-weight: 400;
}

#home-fabricante .txt-registrate {
  color: var(--gris-oscuro-abi);
  font-size: 1.25rem;
  font-weight: 400;
  text-align: center;
  padding: 1rem 0;
}

#home-fabricante .separador {
  height: 1px;
  background-color: #CCC;
  width: 75%;
  margin: 1rem 0;
}

#home-fabricante #requisitos {
  display: flex;
  font-size: 1.125rem;
}

@media screen and (min-width: 600px) {
  #modal-video-fabricante .modal-wrapper {
    width: 70vw !important;
    height: 75vh !important;
  }
}
