.fabricante-home-sidebar{
    --backdrop-opacity: 0!important;
}

.fabricante-home-sidebar .modal-wrapper{
    background: rgba(255,255,255, 0.7);
    z-index: 1001;
    width: 300px;
    left: 0;
    position: absolute;
    display: block;
    border-top-right-radius: 30px;
    backdrop-filter: blur(3px);
    box-shadow: 0 3px 10px rgb(0 0 0 / 20%);
}

.fabricante-home-sidebar-logo {
    position: fixed;
    left: calc(50% - 34% / 2);
    bottom: 0;
    width: 34%;
}

.fabricante-home-sidebar-menu {
    position: relative;
    z-index: 1000;
    margin-top: 15px;
    font-size: 1.125rem;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-self: stretch;
    justify-content: space-evenly;
}

.fabricante-home-sidebar-menu .release-container {
    flex-grow: unset;
}

.fabricante-home-sidebar-menu > a {
    display: flex;
    align-items: center;
    height: 46px;
}

.fabricante-home-sidebar-menu img, .fabricante-home-sidebar-menu svg {
    width: 30px;
    height: 30px;
    object-fit: fill;
    margin-right: 1rem;
}

.fabricante-home-sidebar-menu > a , .fabricante-home-sidebar-menu > div > a {
    font-size: 16px;
    object-fit: fill;
    font-weight: 400;
    color: #4e4e4e!important;
    padding: 8px 10px 8px 2em;
    display: block;
}

.fabricante-home-sidebar-menu > div > span {
    font-size: 16px;
    object-fit: fill;
    font-weight: 400;
    color: #4e4e4e!important;
    padding: 8px 10px 8px 2em;
    display: block;
    min-width: 160px
}

.fabricante-home-sidebar-menu .modo-usuario-menu > a {
    font-size: 16px;
    object-fit: fill;
    font-weight: 400;
    color: #4e4e4e!important;
    padding: 8px 10px 8px 2em;
    display: block;
    min-width: 190px;
}

.fabricante-home-sidebar-menu > strong{
    font-size: 20px;
    object-fit: fill;
    color: #4e4e4e!important;
    padding: 8px 5px 8px 30px;
    display: block;
}

.fabricante-home-sidebar-header {
    display: flex;
    background-color: var(--azul-abi);
}

.fabricante-home-sidebar-header-badge {
    width: 104px;
    height: 104px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fabricante-home-sidebar-header-badge > img {
    object-fit: contain;
    border-radius: 50%;
    border-width: 2px;
    border-color: var(--azul-fabricante) !important;
    border-style: solid;
    width: 75px;
    height: 75px;
}

.fabricante-home-sidebar-header-name {
    font-size: 18px;
    font-weight: bold;
    color: #ffffff !important;
    display: flex;
    justify-content: center;
    height: 104px;
    min-width: 185px;
    flex-direction: column;
}

.fabricante-home-sidebar-header-name > span {
    font-style: italic;
    font-size: 15px;
    max-width: 185px;
    word-wrap: anywhere;
    display: -webkit-box;
}

.fabricante-home-sidebar-header-name > small {
    font-size: 12px;
    margin-bottom: 5px;
    max-width: 185px;
}

.fabricante-home-sidebar-header-name > h2 {
    font-weight: bold;
    color: #ffffff !important;
}
