.registro-header-main-container {
  position: fixed !important;
  height: 110px;
  width: 100%;
  z-index: 400;
}

.image-container-header-home > svg {
  margin-top: 7px;
}

.header-fabricante .boton-header-cliente .icn-notificacion svg {
  width: 30px;
  height: 30px;
}

.header-fabricante .boton-header-cliente .icn-notificacion .cantidad {
  color: var(--naranja-abi);
    position: absolute;
    right: -7px;
    top: -5px;
    font-weight: bold;
    background: var(--azul-fabricante);
    line-height: 0.825rem;
}

.header-fabricante .notificaciones-container {
  width: 400px;
  border-radius: 5px;
  position: absolute;
  height: 350px;
  z-index: 100;
  top: 60px;
  left: -180px;
  background-color: #FFF;
  border: 1px solid #efefef;
  display: flex;
  flex-direction: column;
  cursor: default;
}

.header-fabricante .notificaciones-container::before {
  content:"\A";
  border-style: solid;
  border-width: 10px 15px 10px 0;
  border-color: transparent var(--azul-fabricante) transparent transparent;
  position: absolute;
  left: -15px;
  transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  left: calc(50% - 12px);
  top: -17px;
}

.header-fabricante .notificaciones-container .titulo {
  padding: 0.5rem;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  color: #FFF;
  background-color: var(--azul-fabricante);
  border-radius: 5px 5px 0 0;
}

.header-fabricante .notificaciones-container .lista-notificaciones {
  flex-grow: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.header-fabricante .notificaciones-container .loader-container {
  display: flex;
  flex-grow: 1;
  align-self: stretch;
  align-items: center;
  justify-content: center;
}

.header-fabricante .notificaciones-container .btn-ver-mas {
  padding: 0.5rem;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  color: var(--gris-oscuro-abi);
  background-color: #e0dbe2;
  border-radius: 0 0 5px 5px;
  cursor: pointer;
}

.header-fabricante .notificacion-container {
  padding: 0.5rem;
  border-bottom: 1px solid #e0dbe2;
  display: flex;
  cursor: pointer;
}

.header-fabricante .notificacion-container img {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 1px solid #e0dbe2;
}

.header-fabricante .notificacion-container .titulo-fecha {
  display: flex;
  flex-direction: column;
  color: var(--gris-oscuro-abi);
  padding-left: 0.5rem;
}

.header-fabricante .notificacion-container .titulo-notificacion {
  font-weight: bold;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header-fabricante .notificacion-container .fecha {
  font-size: 0.9rem;
}