.transportista-home-sidebar {
  --backdrop-opacity: 0 !important;
}
.transportista-home-sidebar .modal-wrapper {
  background: rgba(255, 255, 255, 0.7);
  z-index: 1001;
  width: 300px;
  left: 0;
  position: absolute;
  display: block;
  border-top-right-radius: 30px;
  backdrop-filter: blur(3px);
}

.transportista-home-sidebar-menu {
  position: relative;
  z-index: 1000;
  margin-top: 40px;
}

.transportista-home-sidebar-menu > hr {
  border-color: #a1a1a1;
}

.transportista-home-sidebar-menu > div {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  margin-left: 20px;
}
.transportista-home-sidebar-menu > div > img {
  width: 30px;
  height: 30px;
  
}

.transportista-home-sidebar-menu > a,
.transportista-home-sidebar-menu > div > a {
  font-size: 16px;
  object-fit: fill;
  font-weight: 400;
  color: #4e4e4e !important;
  padding: 8px 10px 8px 10px;
  display: block;
}

.transportista-home-sidebar-menu > div > span {
  font-size: 16px;
  object-fit: fill;
  font-weight: 400;
  color: #4e4e4e !important;
  padding: 8px 10px 8px 2em;
  display: block;
  min-width: 160px;
}

.transportista-home-sidebar-menu .modo-usuario-menu > a {
  font-size: 16px;
  object-fit: fill;
  font-weight: 400;
  color: #4e4e4e !important;
  padding: 8px 10px 8px 2em;
  display: block;
  min-width: 190px;
}

.transportista-home-sidebar-menu > strong {
  font-size: 20px;
  object-fit: fill;
  color: #4e4e4e !important;
  padding: 8px 5px 8px 30px;
  display: block;
}

.transportista-home-sidebar-menu .cerrar-sesion {
  color: #ff4c01 !important;
}

.transportista-home-sidebar-header {
  display: flex;
  background-color: #ff4b00;
  height: 100px;
}

.transportista-home-sidebar-header-badge {
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 15px;
}

.transportista-home-sidebar-header-badge .main-container {
  width: 80px;
  height: 80px;
  border-width: 2px;
  border-color: #ff4b00 !important;
  border-style: solid;
  background-color: #ffffff !important;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.transportista-home-sidebar-header-badge .main-container > img {
  object-fit: cover;
  width: 78px;
  height: 78px;
  border-radius: 50%;
}

.transportista-home-sidebar-header-name {
  font-size: 18px;
  font-weight: bold;
  color: #ffffff !important;
  padding-left: 1.5em;
  display: flex;
  justify-content: flex-start;
  align-content: center;
  flex-wrap: wrap;
  margin-top: auto;
  height: auto;
  min-width: 185px;
}
.transportista-home-sidebar-header-name > div {
  overflow: hidden;
  text-overflow: ellipsis;
}
.transportista-home-sidebar-name {
  font-style: italic;
  font-size: 15px;
  padding-right: 5px;
  max-width: 185px;
}

.transportista-home-sidebar-header-name > small {
  font-size: 12px;
  margin-bottom: 5px;
  max-width: 185px;
}

.transportista-home-sidebar-header-name > h2 {
  font-weight: bold;
  color: #ffffff !important;
}

button.alert-button.cancel-button {
  color: #4e4e4e;
}

button.alert-button.confirm-button-transportista {
  color: #ff4b00;
}
